module.exports = "import * as React from 'react';\nimport { Steps, Button } from '../../../../components/';\n\nconst StepsItem = Steps.Item;\n\nexport default class App extends React.Component {\n  state = {\n    current: 0,\n  };\n\n  render() {\n    return (\n      <div>\n        <Button onClick={() => {\n          const current = this.state.current - 1;\n          if (current >= 0) {\n            this.setState({current})\n          } \n        }}></Button>\n        <Button onClick={() => {\n          const current = this.state.current + 1;\n          if (current <= 2) {\n            this.setState({current})\n          }\n        }}></Button>\n        <Steps current={this.state.current}>\n          <StepsItem title=\"\">\n            \n            \n          </StepsItem>\n          <StepsItem title=\"\">\n            \n            \n          </StepsItem>\n          <StepsItem title=\"\">\n            \n            \n          </StepsItem>\n        </Steps>\n      </div>\n    );\n  }\n\n}\n"